	import '../index.scss'
	let interval = null
	export default {
		data() {
			return {
				time:60,
				form:{
					mobile:'',
					code:'',
				}
			}
		},
		components: {

		},
		computed: {

		},
		onLoad() { 

		},
		onReachBottom() {

		},
		destroyed(){
			console.log('组件被销毁');
			clearInterval(interval)
		},
		onHide(){
			clearInterval(interval)
		},
		methods: {
			async sendCode(){
				if(this.form.mobile == ''){
          return uni.showToast({title: '请先输入手机号', icon: 'none'});
				}
				try{
          let info =  await this.$request({
              url: this.$api.myrecharge.sendCode,
							method: 'post',
              data: {mobile: this.form.mobile}
          })
          let { code, data, msg } = info;
          if (code === 1) {
						this.time--
						interval = setInterval(() => {
							this.time--
							if(this.time === 0) {
								clearInterval(interval)
								this.time = 60
							}
						},1000)
          } else {
              uni.showToast({title: msg, icon: 'none'});
          }
        }catch(e){
						uni.showToast({title: '验证码发送失败', icon: 'none'});
            this.$hideLoading();
        }
			},
			inputChange(){
				this.$emit('getForm', this.form)
			}
		}
	}
